/**
 * 
 * primaryColor - 
 * navTheme - sidebar theme ['dark', 'light'] 
 * colorWeak - 
 * layout -  ['sidemenu', 'topmenu'] 
 * fixedHeader -  Header : boolean
 * fixSiderbar -   boolean
 * autoHideHeader -  Header : boolean
 * contentWidth -   |  
 *
 * storageOptions: {} - Vue-ls  (localStorage/sessionStorage)
 *
 */
export default {
  primaryColor: '#1890FF', // primary color of ant design
  navTheme: 'dark', // theme for nav menu
  layoutMode: 'sidemenu', // nav menu position: sidemenu or topmenu
  contentWidth: 'Fixed', // layout of content: Fluid or Fixed, only works when layout is topmenu
  fixedHeader: false, // sticky header
  fixSiderbar: false, // sticky siderbar
  autoHideHeader: false, //  auto hide header
  colorWeak: false,
  multiTab: false,
  language: 'enUS',
  // vue-ls options
  // https://robinck.gitbooks.io/vue-storage/
  storageOptions: {
    namespace: 'tvue__', // key prefix
    name: 'ls', // name variable Vue.[ls] or this.[$ls],
    storage: 'local' // storage name session, local, memory
  }
}
